<template>
    <section v-if="services.length">
        <div class="buttons">
            <button @click="sortByTitle()">{{sort_title_asc ? "A–Å" : "Å–A"}}</button>
            <button @click="sortByTime()" v-if="bus.ita_mode"><fa-icon name="clock-o"/></button>
            <button @click="showSummary = !showSummary">
                <fa-icon name="angle-down" v-if="showSummary"/>
                <fa-icon name="angle-right" v-else/>
            </button>
            <input v-model="filter" type="text" placeholder="søk etter tjenester" >
        </div>
        <template v-if="filteredServices.length">
            <h1>Tjenester:</h1>
            <ul>
                <li v-for="svc in filteredServices" :key="svc.id" :class="['clearfix', svc.state, svc.flag]">
                    <router-link :to="'/node/' + svc.id">{{svc.title}}</router-link>
                    &#32;<span v-if="show_changed" class="changed"><fa-icon name="clock-o"/>&nbsp;{{svc.changed.substr(0,10)}}</span>
                    <a v-if="svc.link" class="svc-link" :href="svc.link"><fa-icon name="external-link"/></a>
                    <p class="summary" v-if="showSummary && svc.summary">{{svc.summary}}</p>
                </li>
            </ul>
        </template>
        <p class="err" v-else>
            Ingen tjenester inneholder "{{filter}}".
        </p>
    </section>
    <section v-else>
        <p class="loading">Loading...</p>
    </section>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

export default {
    props: {
        showSummary: {type: Boolean, default: false}
    },
    data() {
        return {
            services: [],
            show_changed: false,
            sort_title_asc: false,
            filter: "",
            bus: eventBus,
        }
    },
    created() {
        tkdata().then(data => {
            const lst = [];
            Object.values(data.service).forEach(svc => {
                lst.push({
                    id: svc.id,
                    title: svc.title.trim(),
                    summary: svc.text.summary,
                    link: svc.link.home,
                    state: svc.info.service_state,
                    changed: svc.meta.changed,
                    flag: svc.flag,
                });
            });
            this.services = lst;
            this.sortByTitle();
        });
    },
    computed: {
        filteredServices() {
            const filter = this.filter.trim().toLowerCase()
            let services = this.services
            if (!this.bus.ita_mode)
                services = services.filter(svc => svc.state != 'avsluttet');
            if (filter)
                return services.filter(svc => svc.title.toLowerCase().includes(filter));
            return services;
        }
    },
    methods: {
        sortByTitle() {
            this.show_changed = false;
            this.sort_title_asc = !this.sort_title_asc;
            if (this.sort_title_asc) {
                this.services.sort((a,b) => a.title.localeCompare(b.title, 'nb'));
            }
            else {
                this.services.sort((b,a) => a.title.localeCompare(b.title, 'nb'));
            }
        },
        sortByTime() {
            this.show_changed = true;
            this.services.sort((b,a) => a.changed.localeCompare(b.changed, 'nb'));
        },
    },
    components: {
        FaIcon,
    }
}
</script>

